import React from 'react';
import { useSelector } from "react-redux";
// import { Link } from 'react-router-dom';
import ButtonAddCart from '../ButtonAddCart/ButtonAddCart'

function ProductScreen(props) {


    
    const product = props.match.params.id;
    const state = useSelector((state) => ({ ...state.products }));
    const { items, pageIndex } = state
    const meyeuk = items[pageIndex].find(x => x.id == product)
    console.log(meyeuk);
    return (
    <div className='section-content padding-y' style={{ marginTop:'100px'}}> 

        <div className="container-xl">
            <div className="row">

                <div className ='col-6'>
                         <img src={meyeuk.image} alt="" />
                </div>

                <div className='col-6'> 

                    <div className=" rounded-lg bg-light p-3">

                        <div>
                            <ButtonAddCart {...meyeuk} />
                        </div>
                        
                    </div>

                    <div>
                        <div>
                            <h4>
                                 {meyeuk.name}
                                 <p className='text-danger'>{meyeuk.id}</p>
                            </h4>
                        </div>

                        <div> 
                            <b> 
                            Price : {meyeuk.price}
                            </b>
                        </div>

                        <div>
                            Description: 
                            <div>
                                Catégorie :  {meyeuk.category}
                            </div>
                        </div>
                    </div>
                </div>

            </div>
        </div>

    </div>
    )
}


export default ProductScreen;